import GroupSelect from './AdminDelegation/GroupSelect';
import { loadState } from '@nextcloud/initial-state';
export default {
  name: 'AdminDelegating',
  components: {
    GroupSelect: GroupSelect
  },
  data: function data() {
    var availableSettings = loadState('settings', 'available-settings');
    var availableGroups = loadState('settings', 'available-groups');
    var authorizedGroups = loadState('settings', 'authorized-groups');
    return {
      availableSettings: availableSettings,
      availableGroups: availableGroups,
      authorizedGroups: authorizedGroups
    };
  }
};